body{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;height: 100vh;
}
.App {box-sizing: border-box;
  display: flex;
flex-direction: column;
justify-content: center;
align-items: center;border: 2px solid rgba(0,0,0,0.5);
background-color: rgb(119, 111, 111);

}
  .list{
    
    margin: 0 auto;}
    
    li{background-color: #fff;
      box-shadow: 0 0 10px;
      font-size: 20px;
      width: 500px;
      height: 50px;
    border: 2px solid rgba(0,0,0,0.5);
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 0 20px;
   margin: 10px;
    }


ul{list-style: none !important;    
  display: flex;
  flex-direction: column;
  width: 600px;
 
}
.Check{
font-size: 17px;
vertical-align: middle;
cursor: pointer;
color: #fff;
position: relative;
top: -1px;
}
input[type="checkbox"]:checked {
 background-color: green;
 color: #fff;
}
.btnClose{
  width: 20px;
height: 20px;
vertical-align: middle;
background-color: rgb(204, 21, 21);
border: none;
cursor: pointer;
color: #fff;
}
.taskField{
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  span{
    font-size: 25px;
  }
  input{
    width: 430px;
    height: 30px;
padding-left: 10px;
border-radius: 5px;
border:1px solid rgba(0,0,0,0.5)
  }
  ::-webkit-input-placeholder{
        padding-left: 10px;
  }
}
.button_wrap{
  display: flex;
  justify-content: flex-end;
  padding-right: 40px;
  button{
    width: 130px;
    height: 35px;
    border-radius: 5px;
    color: #fff;
    background-color: rgb(97, 97, 228);
    border: none;
    cursor: pointer;
    font-size: 18px;
    &:hover{
      box-shadow: 0 0 10px rgba(0,0,0,0.5);
      transition: 0.3s linear;
    }
  }
}





.done{
  text-decoration: line-through;
}
